@import '_base.sass';

//Mobile up to 1000px
@media only screen and (max-width: 1000px) {
    header {
        @include theme($theme: $color-theme-Darkgreen);
        padding: 4vh 3vw 4vh 3vw;
        display: flex;
        flex-flow: row;
        h1 {
            text-align: left;
            font-size: $font-size-large; }
        nav {
            ul {
                list-style-type: none;
                li {
                    font-size: $font-size-large; }
                a {
                    text-decoration: none; }
                a:link {
                    color: $color-Darkgreen-font; }
                a:visited {
                    color: $color-Darkgreen-font; }
                a:hover {
                    color: $font-accent-color; } }
            img {
                width: 5vw;
                height: 5vh; } }
        .menuItem {
            display: block;
            padding: 2vh 0 2vh 0; }
        .hamburger {
            justify-items: end;
            position: fixed;
            z-index: 5000;
            border: none;
            padding-rigth: 1vw;
            rigth: 0;
            cursor: pointer; }
        .closeOpt {
            display: none; }
        .globalNav {
            display: flex;
            flex-flow: columns;
            align-items: center;
            position: fixed;
            transform: translateY(-100%);
            transition: transform 0.2s;
            top: 0;
            left: 0;
            rigth: 0;
            bottom: 0;
            width: 100vw;
            z-index: 4999;
            padding: 10vh 5vw 5vh 5vw; }
        //JS class
        .menuVisability {
            transform: translateY(0); } } }

